import { useState, useEffect, useRef } from "react";
import Link from "next/link";
import { supabase } from "../lib/supabaseClient";
import { FaSearch } from "react-icons/fa";

export default function Navbar() {
  const [searchTerm, setSearchTerm] = useState("");
  const [suggestions, setSuggestions] = useState<{ id: string; title: string }[]>([]);
  const [allGames, setAllGames] = useState<{ id: string; title: string }[]>([]);
  const searchRef = useRef<HTMLDivElement>(null);

  // Buscar todos os jogos uma vez
  useEffect(() => {
    async function fetchGames() {
      const { data, error } = await supabase.from("games").select("id, title");
      if (error) return console.error("Erro ao buscar jogos:", error);
      setAllGames(data || []);
    }
    fetchGames();
  }, []);

  // Filtrar sugest천es
  useEffect(() => {
    if (!searchTerm) return setSuggestions([]);
    const filtered = allGames
      .filter((g) => g.title.toLowerCase().includes(searchTerm.toLowerCase()))
      .slice(0, 5); // limitar a 5 sugest천es
    setSuggestions(filtered);
  }, [searchTerm, allGames]);

  // Fechar sugest천es ao clicar fora
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (searchRef.current && !searchRef.current.contains(event.target as Node)) {
        setSuggestions([]);
      }
    };
    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  return (
    <nav className="flex items-center justify-between bg-gray-900 p-4 shadow-md relative z-50">
      <Link href="/" className="text-2xl font-bold text-white">
        GamersClub
      </Link>

      {/* Busca */}
      <div className="relative w-1/3" ref={searchRef}>
        <div className="flex items-center bg-white rounded-lg overflow-hidden">
          <FaSearch className="text-gray-500 mx-2" />
          <input
            type="text"
            placeholder="Pesquisar jogos..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="w-full p-2 outline-none text-black"
          />
        </div>

        {/* Sugest천es */}
        {suggestions.length > 0 && (
          <ul className="absolute top-full left-0 w-full bg-white text-black rounded-b-lg shadow-lg max-h-60 overflow-auto z-50">
            {suggestions.map((game) => (
              <li
                key={game.id}
                className="p-2 cursor-pointer hover:bg-gray-200"
                onClick={() => {
                  setSearchTerm("");
                  setSuggestions([]);
                  window.location.href = `/games/${game.id}`; // redireciona
                }}
              >
                {game.title}
              </li>
            ))}
          </ul>
        )}
      </div>

      <div className="flex space-x-4">
        <Link href="/games" className="text-white hover:text-blue-400">
          Jogos
        </Link>
        <Link href="/sobre" className="text-white hover:text-blue-400">
          Sobre
        </Link>
      </div>
    </nav>
  );
}
